<template>
  <div style="text-align: center">
    <div class="profile-area">
      <div class="profile-detail">
        <img style="float: left; height: auto; width: 200px" src="https://avatars2.githubusercontent.com/u/17725948?s=460&v=4">
        <div style="line-height: 28px">
          <span style="padding-left: 20px; font-family: Arial; font-weight: 200; font-size: 40px; color: #282828">Benny Ray</span><br/>
          <span style="padding-left: 20px; font-family: Arial; font-weight: 100; font-size: 20px; color: #505050">Master of Engineering | SCUT</span><br/>
          <span style="padding-left: 20px; font-family: Arial; font-weight: 100; font-size: 20px; color: #505050">Interests:  Artificial Intelligence & Neuro-Informatics</span><br/>
        </div>
        <div style="margin-top: 16px; line-height: 28px">
          <a class="github-link" href="https://github.com/Raniac/NEURO-LEARN" target="_blank">NEURO-LEARN</a><br/>
          <a class="github-link" href="https://github.com/Raniac" target="_blank">GitHub/Raniac</a><br/>
          <a class="github-link" href="mailto:leibingye@outlook.com">leibingye@outlook.com</a>
        </div>
      </div>
    </div>
    <div class="projects-area">
    <div>
      <h1 style="padding-left: 20px; font-family: Arial; font-weight: 150; font-size: 30px; color: #505050">Joined Projects | {{ username }}</h1>
    </div>
    <div style="margin: 14px">
      <el-table
        class="projects-table"
        :data="projects_table"
        stripe
        border
        style="width: 100%; background-color: #E8E8E8; color: #282828; ">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" in-line class="projects-table-expand">
              <el-form-item label="Introduction">
                <span>{{ props.row.fields.introduction }}</span>
              </el-form-item>
              <el-form-item label="Methods">
                <span>{{ props.row.fields.methods }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
        label="Project ID"
        prop="fields.proj_id">
        </el-table-column>
        <el-table-column
        label="Project Title"
        prop="fields.title">
        </el-table-column>
        <el-table-column
        label="Project Label"
        prop="fields.label">
        </el-table-column>
      </el-table>
    </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  mounted () {
    this.username = sessionStorage.getItem('Username')
    this.showProjects()
  },
  data () {
    return {
      search_input: '',
      selected_status: '',
      username: '',
      projects_table: []
    }
  },
  methods: {
    showProjects () {
      axios.get('/api/v0/show_project_overview')
        .then(response => {
          var res = response.data
          if (res.error_num === 0) {
            console.log(res)
            this.projects_table = res['list']
            console.log(this.projects_table)
          } else {
            this.$message.error('Failed!')
            console.log(res['msg'])
          }
        })
    }
  }
}
</script>

<style lang="scss">
.profile-area {
  width: 772px;
  margin: 14px auto;
  text-align: left;
  padding: 14px;
  background-color: #FFFFFF;
  .profile-detail {
    padding: 14px;
    min-height: 200px;
    .github-link {
      padding-left: 20px;
      font-family: Arial;
      font-weight: 100;
      font-size: 20px;
      color: #282828;
      &:hover {
      color: #00CCFF;
      }
    }
  }
}
.projects-area {
  width: 772px;
  margin: 14px auto;
  padding: 14px;
  text-align: left;
  background-color: #FFFFFF;
}
.projects-table-expand {
  font-size: 0;
  width: 100%;
  }
  .projects-table-expand label {
    width: 140px;
    color: #505050;
  }
  .projects-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
</style>



